.flatpickr-input, .flatpickr-wrapper input {
  z-index: 1;
  cursor: pointer; }

.flatpickr-wrapper {
  position: absolute;
  display: none; }

.flatpickr-wrapper.inline, .flatpickr-wrapper.inline .flatpickr-calendar, .flatpickr-wrapper.static {
  position: relative; }

.flatpickr-wrapper.static .flatpickr-calendar {
  position: absolute; }

.flatpickr-wrapper.inline, .flatpickr-wrapper.open {
  display: inline-block; }

.flatpickr-wrapper.inline .flatpickr-calendar, .flatpickr-wrapper.open .flatpickr-calendar {
  z-index: 99999;
  visibility: visible; }

.flatpickr-calendar {
  background: #fff;
  border: 1px solid #ddd;
  font-size: 90%;
  border-radius: 3px;
  position: absolute;
  top: 100%;
  left: 0;
  visibility: hidden;
  width: 256px; }

.flatpickr-calendar.hasWeeks {
  width: 288px; }

.flatpickr-calendar.hasWeeks .flatpickr-weekdays span {
  width: 12.5%; }

.flatpickr-calendar:after, .flatpickr-calendar:before {
  position: absolute;
  display: block;
  pointer-events: none;
  border: solid transparent;
  content: '';
  height: 0;
  width: 0;
  left: 22px; }

.flatpickr-calendar:before {
  border-width: 5px;
  margin: 0 -5px; }

.flatpickr-calendar:after {
  border-width: 4px;
  margin: 0 -4px; }

.flatpickr-calendar.arrowTop:after, .flatpickr-calendar.arrowTop:before {
  bottom: 100%; }

.flatpickr-calendar.arrowTop:before {
  border-bottom-color: #ddd; }

.flatpickr-calendar.arrowTop:after {
  border-bottom-color: #fff; }

.flatpickr-calendar.arrowBottom:after, .flatpickr-calendar.arrowBottom:before {
  top: 100%; }

.flatpickr-calendar.arrowBottom:before {
  border-top-color: #ddd; }

.flatpickr-calendar.arrowBottom:after {
  border-top-color: #fff; }

.flatpickr-month {
  background: 0 0;
  color: #000;
  padding: 4px 5px 2px;
  text-align: center;
  position: relative; }

.flatpickr-next-month, .flatpickr-prev-month {
  text-decoration: none;
  cursor: pointer;
  position: absolute;
  top: .5rem; }

